@tailwind base;
@tailwind components;
@tailwind utilities;

/* index.css */

/* Ustawienia płynnego przewijania dla całej strony */
html {
  scroll-behavior: smooth;
  overflow-x: hidden; /* Zapobiega poziomemu przewijaniu */
}

body {
  overflow-x: hidden; /* Zapobiega poziomemu przewijaniu */
}

/* Stylizowany pasek przewijania */
::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f8e1e7; /* Jasnoróżowe tło paska przewijania */
}

::-webkit-scrollbar-thumb {
  background-color: #E63946; /* Czerwony akcent przewijania */
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #d62839; /* Ciemniejszy czerwony po najechaniu */
}

/* Dla Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #E63946 #f8e1e7;
}

/* Styl dla całej strony */
body {
  font-family: 'Host Grotesk', sans-serif;
  background-color: #fff5f7;
  color: #2d2d2d;
}

/* Globalne przyciski */
.button {
  background-color: #E63946;
  color: white;
  padding: 12px 24px;
  border-radius: 30px;
  transition: all 0.3s ease-in-out;
}

.button:hover {
  background-color: #d62839;
  transform: scale(1.05);
}
